html,
body {
  height: 100%;
  margin: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
}

#root {
  margin: 0 auto;
  height: 100%;
}

.container {
  display: flex;
  color: #fff;
  height: 100%;
}

.blue-container {
  display: flex;
  flex: 0 0 50%;
  background-color: #77b5fe;
  align-items: center;
  justify-content: center;
}
.pink-container {
  display: flex;
  flex: 0 0 50%;
  background-color: #ffc0cb;
  align-items: center;
  justify-content: center;
}

h1 {
  font-weight: normal;
  letter-spacing: 0.125rem;
  text-transform: uppercase;
}

.countdown-container {
  position: fixed;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 3em;
}

iframe {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  border: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  z-index: 999999;
}
